:root {
    --text-color:  rgb(59, 59, 59);
}

@font-face {
    font-family: ImpactLabelReversed;
    src: url("../assets/fonts/impact_label/impact_label_reversed.ttf");
}

@font-face {
    font-family: BonVoyage;
    src: url("../assets/fonts/made_bon_voyage/bon_voyage.otf");
}


body {
    background-image: url("../assets/images/ep_naturalwhite.png");
    background-size: auto;
    background-repeat: repeat;
}
img {
    filter: saturate(0%) contrast(130%) ;
    border: 5px solid var(--text-color);
    transition: filter 500ms ease-in-out;
}
img:hover {
    filter: saturate(20%) contrast(160%) ;
    transition: filter 500ms ease-in-out;
}
p {
    font-family: BonVoyage;
    font-weight: 500;
    font-size: large;
}
.header {

    color: var(--text-color);
    margin-bottom: 0;
    font-family: ImpactLabelReversed;
}
.navbar-menu {
    border: 2px solid var(--text-color) ;
    border-left: 0;
    border-right: 0;

    text-transform: uppercase;
    font-family: ImpactLabelReversed;
    font-size: xx-large;
}

.navbar-menu__item {
 position: relative;
 color: var(--text-color);
 margin: auto;
}

.navbar-menu__item > a {
    position: relative;
    z-index: 2;
}

.main-content {
    background: transparent;
}
.navbar-menu__item--circle {
    fill: none;
    stroke:#9b1c1c;
    stroke-width: 4px;
    opacity: 80%;
}
.navbar-menu__item--circle-1 {
    stroke-dasharray: 425.6;
    stroke-dashoffset: 426;
}
.navbar-menu__item--circle-2 {
    stroke-dasharray: 419.8;
    stroke-dashoffset: 420;
    stroke: #092768;
    opacity: 80%;
}
.navbar-menu__item--circle-3 {
    stroke-dasharray: 515.8;
    stroke-dashoffset: 516;
    stroke: #093f33;
    opacity: 80%;
}
.svg-container {
    z-index: 1;
    position: absolute;
}
.svg-container1 {
    top: -70px;
    left: -250px;
    width: 600px;
    height: 200px;
}
.svg-container2 {
    top: -65px;
    left: -205px;
    width: 400px;
    height: 200px;
}
.svg-container3 {
    top: -70px;
    left: -250px;
    width: 500px;
    height: 200px;
}